function toCamelCase(name) {
    const countryNameMap = {
        heardIslandAndMcDonaldIslands: 'Heard Island And McDonald Islands',
        sainthElenaAscensionAndTristanDaCunha: 'Saint Helena, Ascension And Tristan Da Cunha',
        saoTomeandPrincipe: 'So Tom And Prncipe',
        reunion: 'Runion' || 'runion',
        cocosIslands: 'Cocos (Keeling) Islands',
        alandIslands: 'land Islands',
        saintBarthelemy: 'Saint Barthlemy',
      // Agrega otros nombres de pases segn sea necesario
    };
  
    const formattedName = name.toLowerCase();
  
    // Condiciones para manejar nombres de pases especficos
    if (countryNameMap.hasOwnProperty(formattedName)) {
      return countryNameMap[formattedName];
    }
  
    // Resto de la lgica para convertir a camelCase
    return formattedName
      .replace(/[^a-zA-Z0-9]+(.)/g, (match, character) => character.toUpperCase())
      .replace(/[^a-zA-Z0-9]/g, "")
      .replace(/[^a-zA-Z0-9\s]/g, "")
      .replace(/\s/g, "")
      .replace(/,\s*/g, "");
  }
  
  // Mantn el export default al final del archivo
  export default toCamelCase;